if ($(window).width() <= 768) {
    $('#movies-rec-box').removeClass('swiper--pagination-hidden');
}

else {
    $('#movies-rec-box').addClass('swiper--pagination-hidden');
}

$(window).resize(function () {
    if ($(window).width() <= 768) {     
        $('#movies-rec-box').removeClass('swiper--pagination-hidden');
    }
    
    else {
        $('#movies-rec-box').addClass('swiper--pagination-hidden');
    }
});

var MoviesRecBox = new Swiper('#movies-rec-box .swiper-container', {
    slidesPerView: 6,
    slidesPerGroup: 6,
    spaceBetween: 15,
    autoplay: {
        delay: 4500,
        disableOnInteraction: false,
    },
    
    pagination: {
        el: '#movies-rec-box .swiper-pagination',
        clickable: true,
    },

    navigation: {
        nextEl: '#movies-rec-box .swiper-button-next',
        prevEl: '#movies-rec-box .swiper-button-prev',
    },

    breakpoints: {
        480: {
            slidesPerView: 2,
            slidesPerGroup: 2,
        },
        736: {
            slidesPerView: 3,
            slidesPerGroup: 3,
        },
        1024: {
            slidesPerView: 4,
            slidesPerGroup: 4,
        },
    }
});